import { GetRequiredEnergy } from './creepController';
import { Harvest } from './harvest';
import { RandomInt } from './util';
export const HarvesterController = {
    type: "harvester" /* Harvester */,
    minEnergy: GetRequiredEnergy(['work', 'move']),
    create(spawn, name, maxEnergy) {
        const body = ['work', 'move'];
        const maxCount = Math.floor((maxEnergy - this.minEnergy) / BODYPART_COST.work);
        if (maxCount > 0) {
            const count = RandomInt(maxCount + 1);
            if (count > 0) {
                body.splice(1, 0, ...new Array(count).fill('work'));
            }
        }
        return spawn.spawnCreep(body, name);
    },
    ticker(creep) {
        return Harvest(creep);
    },
};
